<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>分支机构</h5>
      </div>

      <div class="ibox-content">
        <edit-form v-bind:hasOperation="hasOperation" v-bind:formTitle="'心理学院'" v-bind:data="form" v-bind:keys="keys1" @confirm="edit"></edit-form>
        <edit-form v-bind:hasOperation="hasOperation" v-bind:formTitle="'脑科学与康复医学研究院'" v-bind:data="form" v-bind:keys="keys2" @confirm="edit"></edit-form>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'collegeIntroductionBranch',
  data () {
    return {
      hasOperation: false,
      form: {},
      keys1: [
        { label: '负责人', key: 'psychological_college_principal' },
        { label: '固定电话', key: 'psychological_college_tel' },
        { label: '传真', key: 'psychological_college_fax' },
        { label: '地址', key: 'psychological_college_principal_address' }
      ],
      keys2: [
        { label: '负责人', key: 'brain_science_college_principal' },
        { label: '固定电话', key: 'brain_science_college_tel' },
        { label: '传真', key: 'brain_science_college_fax' },
        { label: '地址', key: 'brain_science_college_principal_address' }
      ]
    }
  },
  methods: {
    edit (form) {
      Service.EssentialInformation.edit(this.form.id, form).then(response => {
        if (response.code == 200) {
          this.getDetails()
        }
      })
    },
    getDetails () {
      Service.EssentialInformation.get().then(response => {
        if (response.code == 200) {
          this.form = response.data
        }
      })
    }
  },
  created () {
    if (Cache.hasPermission('basic_information')) {
      this.hasOperation = true
    }
    this.getDetails()
  }
}
</script>

<style scoped>

</style>



